<template>
  <div class="searchAddress_container">
      <head-top head-title="搜索" goBack="true"></head-top>
      <form class="search_form">
          <input type="search" class="" placeholder="请输入搜索" v-model="searchValue">
          <!-- @click.prevent取消默认表单提交事件 -->
          <button @click.prevent="searchPlace">搜索</button>
      </form>
      <div class="empty_list" v-if="searchDate">
          <ul>
              <li v-for="(item,index) in searchDate" :key="index" @click="choooedAddress(item)">
                  <h4>{{item.name}}</h4>
                  <p>{{item.address}}</p>
              </li>
          </ul>
      </div>
       <div  class="empty_tips" v-else>
            <p>找不到地址？</p>
            <p>尝试输入小区、写字楼或学校名</p>
            <p>详细地址（如门牌号等）可稍后输入哦</p>
        </div>
  </div>
</template>

<script>
import {searchNearby} from '../../../../../service/getData'
import headTop from '../../../../../components/header/header'
import {mapMutations} from 'vuex'
export default {
    components: {
        headTop
    },data(){
        return{
            searchValue:null,//搜索内容
            searchDate:null//搜索结果
        }
    },
    methods: {
        ...mapMutations([
            'CHOOSE_SEARCH_ADDRESS'
        ]),
        //搜索
        async searchPlace()
        {
            if(this.searchValue)
            {
                this.searchDate=await searchNearby(this.searchValue);
            }
        },
        //搜索结果
        choooedAddress(item)
        {
            this.CHOOSE_SEARCH_ADDRESS(item);
            this.$router.go(-1);
        }
    }
}
</script>

<style lang="scss" scoped>
@import '../../../../../style/mixin.scss';
.searchAddress_container{
    position: fixed;
    top:0;
    left:0;
    right: 0;
    bottom: 0;
    z-index:230;
    overflow-y: auto;
    padding-top: 1.95rem;
    p, span{
            font-family: Helvetica Neue,Tahoma,Arial;
    }
background: #ffffff;
}
.search_form{
    padding:0.6rem;
    display: flex;
    input{
        height: 1.5rem;
        background-color: #f1f1f1;
        border: 1px;
        flex:4;
        border-radius: 0.15rem;
        @include sc(0.6rem,#999)
    }
    button{
        flex:1;
        margin-left:0.3rem;
        background: $blue;
        @include sc(0.65rem,#fff);
        border-radius: 0.15rem;
    }
}
.empty_tips{
  width: 100%;
  position: absolute;
  top:50%;
  p{
      @include sc(0.6rem,#999);
      padding-top: 0.3rem;
  }
  text-align: center;
  transform: translateY(-50%)

}
.empty_list{
    margin-top:1.5rem;
    ul{
        li{
            padding:0.6rem;
            h4{
                font-size: 0.8rem;
            }
            p{
                @include sc(0.7rem,#666);
            }
            margin-bottom: 0.3rem;
            border-bottom:0.025rem solid #f1f1f1;
        }
    }
}
</style>



